header{
    text-align: center;
}
header h5{
    font-style: italic;
}
.juegos{
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    flex-wrap: wrap;
}
.cardJuego{
    width: 300px;
    border: 1px solid brown;
    padding: 0px 15px 5px 15px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-bet;
}
.cardJuego:hover{
    box-shadow:  2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.cardJuego p{
    text-align: justify;
}
.cardJuego img{
    max-width:100%
}
.cambioColor{
    color:cadetblue
}

.square {
    height: 50px;
    width: 50px;
    background-color: #555;
    display: flex;
}

/*DEMO JUEGO*/
.hoja{
    background:#06A847;
    width:100px;
    height:100px;
    top:-50px;
    left:-40px;
    border-radius:80% 90% 80% 90%;
    clip-path: polygon(0% 0%, 40% 0%, 50% 50%, 60% 0%, 100% 0, 100% 100%, 0 100%);
}
.rana{
    width: 140px;
    position: absolute;
}

.ojoizq, .ojoder{
    height: 30px;
    width: 30px;
    border: 6px solid  #FE5F55;
    border-radius: 50%;
    position: absolute;
    background: white;
}
.ojoder{
    margin: -20px 0px 0px 88px;
    outline: 4px solid black;
    outline-offset: -30px;
}
.ojoizq{
    margin: -20px 0px 0px 10px;
    outline: 4px solid black;
    outline-offset: -30px;
}
.caraRana{
    width: 140px;
    height: 100px;
    border-radius: 50px;
    background: radial-gradient(circle at 50% -130px, #FE5F55 200px, #A64942 200px);
}

/*GRID PARA EL DEMO*/
.parent {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 15px;
    }
    
    .div1 { grid-area: 1 / 1 / 2 / 2; }
    .div2 { grid-area: 1 / 2 / 2 / 3; }
    .div3 { grid-area: 1 / 3 / 2 / 4; }
    .div4 { grid-area: 1 / 4 / 2 / 5; }
    .div5 { grid-area: 2 / 1 / 3 / 2; }
    .div6 { grid-area: 2 / 2 / 3 / 3; }
    .div7 { grid-area: 2 / 3 / 3 / 4; }
    .div8 { grid-area: 2 / 4 / 3 / 5; }
    .div9 { grid-area: 3 / 1 / 4 / 2; }
    .div10 { grid-area: 3 / 2 / 4 / 3; }
    .div11 { grid-area: 3 / 3 / 4 / 4; }
    .div12 { grid-area: 3 / 4 / 4 / 5; }
    .div13 { grid-area: 4 / 1 / 5 / 2; }
    .div14 { grid-area: 4 / 2 / 5 / 3; }
    .div15 { grid-area: 4 / 3 / 5 / 4; }
    .div16 { grid-area: 4 / 4 / 5 / 5; }
    .div17 { grid-area: 4 / 5 / 5 / 6; }

#juego{
    margin: 20px 50px 20px 50px;
}

.hidden{
    display:none;
}